.nav-link {
  display: block;
  background-color: none;
  color: whitesmoke;
  text-decoration: none;
  margin-top: 8px;
}

.nav-link:hover {
  color: darkblue;
}

.user {
  margin-right: 10px;
}

.menu {
  margin-left: 10px;
  color: whitesmoke;
  margin-top: 8px;
  padding-bottom: 0;
}

.menu-view {
  position: absolute;
  background-color:rgba(0,0,0,0.95);
  height: 100px;
  left: -50vw;
  width: 50vw;
  height: 100vh;
  transition: 1s;
}

.menu:active {
  color: red;
}

.menu:hover {
  color: darkblue;
}

.title {
  margin-left: 10px;
  color: whitesmoke;
}

.nav-container {
  background-color: black;
  height: 100%;
}

.nav-link-container {
  height: 50px;
}

.nav-flex {
  display: flex;
}

.fill {
  flex-grow: 1; 
}