.header {
  display: flex;
}

.header-text {
  margin-left: 20px;
  color: whitesmoke;
}

.option-container {
  text-align: left;
  overflow: hidden;
}

.fill {
  flex-grow: 1;
}

.close-button {
  background-color: transparent;
  color: whitesmoke;
  border: none;
  margin-right: 20px;
}

.close-button:hover {
  color: darkblue;
}

.seperator {
  height: 5px;
  background-color: whitesmoke;
  margin: 0 20px 0 20px;
}

.menu-option {
  width: 60%;
  height: 50px;
  margin-bottom: 10px;
  border: none;
  background: linear-gradient(to left, transparent 50%, darkblue 50%);
  color: whitesmoke;
  font-size: 20pt;
  text-align: left;
  margin-left: 20px;
  background-size: 200% 100%;
  background-position: right bottom;
  transition: all 0.3s ease;
  width: 100%;
}

.menu-option:hover {
  background-position: left bottom
}